<script>
	import { onMount, onDestroy, setContext } from 'svelte';
	import { createEventDispatcher } from 'svelte';

	import {
		key,
		width,
		height,
		canvas as canvasStore,
		context as contextStore,
		pixelRatio,
		props,
		time,
		tileSize,
		cam,
	} from './game.js';

	export let killLoopOnError = true;
	export let attributes = {};

	let clickStart;
	let mouseDown = false;
	let mouseX = 0;
	let mouseY = 0;

	let listeners = [];
	let canvas;
	let context;
	let frame;

	onMount(() => {
		// prepare canvas stores
		context = canvas.getContext('2d', attributes);
		canvasStore.set(canvas);
		contextStore.set(context);

		// start game loop
		return createLoop((elapsed, dt) => {
			time.set(elapsed);
			render(dt);
		});
	});

	setContext(key, {
		add (fn) {
			this.remove(fn);
			listeners.push(fn);

			if (fn.setup) {
				let p = fn.setup($props);
				if (p && p.then) p;
			}
			fn.ready = true;

			listeners.sort((e1, e2) => e1.height - e2.height);
		},
		remove (fn) {
			const idx = listeners.indexOf(fn);
			if (idx >= 0) listeners.splice(idx, 1);
		}
	});

	function render (dt) {
		context.save();

		context.translate(-$cam.dx, -$cam.dy);
		context.scale($pixelRatio, $pixelRatio);
		listeners.forEach(entity => {
			try {
				if (entity.mounted && entity.ready && entity.render) {
					entity.render($props, dt);
				}
			} catch (err) {
				console.error(err);
				if (killLoopOnError) {
					cancelAnimationFrame(frame);
					console.warn('Animation loop stopped due to an error');
				}
			}
		});
		context.restore();
	}

	function handleResize () {
		width.set(window.innerWidth);
		height.set(window.innerHeight);
		pixelRatio.set(window.devicePixelRatio);
	}

	function createLoop (fn) {
		let elapsed = 0;
		let lastTime = performance.now();
		(function loop() {
			frame = requestAnimationFrame(loop);
			const beginTime = performance.now();
			const dt = (beginTime - lastTime) / 1000;
			lastTime = beginTime;
			elapsed += dt;
			fn(elapsed, dt);
		})();
		return () => {
			cancelAnimationFrame(frame);
		};
	}

	const dispatch = createEventDispatcher();
	function handleMouseUp(ev) {
		mouseDown = false;

		let x = ev.x + $cam.dx/$pixelRatio;
		let y = ev.y + $cam.dy/$pixelRatio;

		let timeDis = Date.now() - clickStart;

		let data = {
			x: x,
			y: y,
			tileX: x / $tileSize,
			tileY: y / $tileSize
		}
		if(timeDis < 200) dispatch('click', data);
	}

	function handleMouseMove(event) {
		if(mouseDown) {
			$cam = {
				dx: $cam.dx + (mouseX - event.clientX) * $pixelRatio,
				dy: $cam.dy + (mouseY - event.clientY) * $pixelRatio
			}
		}

		mouseX = event.clientX;
		mouseY = event.clientY;
	}

	function handleMouseDown(event) {
		mouseDown = true;
		clickStart = Date.now();
	}
</script>

<canvas
	on:mousemove={handleMouseMove}
	on:mousedown={handleMouseDown}
	on:mouseup={handleMouseUp}
	bind:this={canvas}
	width={$width * $pixelRatio}
	height={$height * $pixelRatio}
	style="width: {$width}px; height: {$height}px;"
/>
<svelte:window on:resize|passive={handleResize} />
<slot></slot>

