<script>
  import { game } from './stores.js';
  import { sendShowRoom, sendShowDoor, sendHideDoor, sendHideRoom } from './WebHandler.js';
  import Canvas from './visuals/Canvas.svelte';
  import Grid from './visuals/Grid.svelte';
  import Background from './visuals/Background.svelte';
  import Room from './visuals/Room.svelte';
  import Hover from './visuals/Hover.svelte';
  import Door from './visuals/Door.svelte';
  import GameInfoBar from './GameInfoBar.svelte';
  import { tileSize, cam, pixelRatio } from './visuals/game.js';

  let hover = {x: 0, y: 0, w: 1, h: 1};

  function onClick(ev) {
		let data = ev.detail;
		let room = $game.dungeon.rects.find(room => data.tileX >= room.x && data.tileX < room.x + room.w && data.tileY >= room.y && data.tileY <= room.y + room.h);
    let door = $game.dungeon.doors.find(room => Math.floor(data.tileX) == room.x && Math.floor(data.tileY) == room.y);

		if(room) {
      if(!room.visible) {
        sendShowRoom(room, $game.id, $game.thisPlayer.id);
        if(door) sendShowDoor(door, $game.id, $game.thisPlayer.id);
      } else {
        sendHideRoom(room, $game.id, $game.thisPlayer.id);
        if(door) sendHideDoor(door, $game.id, $game.thisPlayer.id);
      }
    }
	}

	function mousemove(ev) {
		let mouse = {x: (ev.clientX + $cam.dx/$pixelRatio) / $tileSize, y: ((ev.clientY+$cam.dy/$pixelRatio)) / $tileSize}
		hover = $game.dungeon.rects.find(room => mouse.x >= room.x && mouse.x < room.x + room.w && mouse.y >= room.y && mouse.y <= room.y + room.h);
  }
</script>


<GameInfoBar />

{#if $game.dungeon.init}
<div id="canvasDisplay" on:mousemove={mousemove}>
  <Canvas on:click={onClick}>
  <Background>
  </Background>

  {#each $game.dungeon.rects as room}
    <Room dimension={room} />
  {/each}

  {#each $game.dungeon.doors as room}
    <Door bind:dimension={room} />
  {/each}

  <Grid />
    {#if $game.thisPlayer.isMaster}
      <Hover bind:dimension={hover} />
    {/if}
  </Canvas>
</div>
{/if}
