<script>
	import { createGame, openSocket, joinGame } from './WebHandler.js';
	import { game } from './stores.js';
	import { setError, resetError } from './errorstore.js';

	let gameName = "";
	let playerName = "";

	async function clickCreate() {
		let answer = await createGame(gameName, playerName);

		if(!answer.isError) {
			$game = {id: answer.game.id, name: answer.game.name, playerNames: [{name: playerName, isMaster: true, isYou: true}], thisPlayer: { id: answer.player.id, name: playerName, isMaster: true, isYou: true }, dungeon: answer.dungeon};
			openSocket($game.id, $game.thisPlayer.id);
			resetError();
		} else {
			setError(answer.error);
		}
	}

	async function clickJoin() {
		let answer = await joinGame(gameName, playerName);

		if(!answer.isError) {
			$game = {id: answer.id, name: answer.gameName, playerNames: answer.player, thisPlayer: { id: answer.playerID, name: playerName, isMaster: false, isYou: true }, dungeon: answer.dungeon};
			openSocket($game.id, $game.thisPlayer.id);
			resetError();
		} else {
			setError(answer.error);
		}
}
</script>

<div class="main card">
	<div class="row">
		<div class="col">
	  <input placeholder="Enter Game Name" class="is-rounded" bind:value={gameName} />
		</div>
	</div>
	<div class="row">
		<div class="col">
			<input placeholder="Enter your Name" class="is-rounded" bind:value={playerName} />
		</div>

		<div class="col-2">
			<button class="button is-full-width" on:click={clickJoin}> Join Game </button>
		</div>

		<div class="col-2">
			<button class="button primary is-full-width" on:click={clickCreate}> Create Game </button>
		</div>
	</div>
</div>

<style>
.main {
	width: 75%;
	margin-top: 32px;
	margin-left: auto;
  margin-right: auto;
	background: var(--bg-secondary-color);
}
</style>
